import React from "react";
import DetailPresenter from "./DetailPresenter";
import { movieApi, tvApi } from "api";

export default class extends React.Component {
  constructor(props) {
    super(props);
    const {
      location: { pathname },
    } = props;
    this.state = {
      result: null,
      error: null,
      loading: true,
      isMovie: pathname.includes("/movie/"), // MovieÏù∏ÏßÄ ÌôïÏù∏
    };
  }

  async componentDidMount() {
    const {
      match: {
        params: { id },
      },
      history: { push },
    } = this.props;
    /* isMovieÎßå ÏûàÏñ¥ÎèÑ ÎêòÎäî Ïù¥Ïú† movie, tvÎßå Ïù∏ÏãùÌïòÍ≥† ÎÇòÎ®∏ÏßÄÎäî RouteÏóê ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê
    sdfsfs Ïù¥Îü∞ urlÏùÄ homeÏúºÎ°ú redirect Îê† Í±∞ÏûÑ. */
    const { isMovie } = this.state;
    /* id Ï≤òÎ¶¨ */
    const parsedId = parseInt(id);
    if (isNaN(parsedId)) {
      return push("/"); // idÍ∞Ä NaNÏù¥Î©¥ ÌôàÏúºÎ°ú Í∞ê.
    }
    let result = null;
    try {
      if (isMovie) {
        ({ data: result } = await movieApi.movieDetail(parsedId));
      } else {
        ({ data: result } = await tvApi.showDetail(parsedId));
      }
    } catch {
      this.setState({
        error: "Can't find anything.",
      });
    } finally {
      this.setState({ loading: false, result });
    }
  }

  render() {
    const { result, error, loading } = this.state;
    return <DetailPresenter result={result} error={error} loading={loading} />;
  }
}
