import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import Home from "Routes/Home";
import TV from "Routes/TV";
import Search from "Routes/Search";
import Header from "Components/Header";
import Detail from "Routes/Detail";

export default () => (
  <Router>
    <Header />
    <Switch>
      <Route
        path="https://gangyong92.github.io/Nomflix/"
        exact
        component={Home}
      />
      <Route path="https://gangyong92.github.io/Nomflix/tv" component={TV} />
      <Route
        path="https://gangyong92.github.io/Nomflix/search"
        component={Search}
      />
      <Route
        path="https://gangyong92.github.io/Nomflix/movie/:id"
        component={Detail}
      />
      <Route
        path="https://gangyong92.github.io/Nomflix/show/:id"
        component={Detail}
      />
      <Redirect from="*" to="/" />
    </Switch>
  </Router>
);
